// Import Reducer type
import { Reducer } from 'redux';
import {
	UserActions,
	UserActionTypes
} from './actions';

import { IUser, IUsersState } from './types'

// const initialUser: IUser = {
// 	userId: 0,
// 	name: '',
// 	role: 'Admin',
// 	department: 'Development',
// 	createdBy: 0,
// 	created: new Date()
// };

const initialUsersState: IUsersState = {
	userGroups: [],
	groupOptions: [],
	userOptions: [],
	user: undefined,
	loading: false,
	formMode: 'display',
	groupIdEditing: 0,
	isDetail: false

};


export const userReducer: Reducer<IUsersState, UserActions> = (
	state = initialUsersState,
	action
) => {
	switch (action.type) {

		case UserActionTypes.GET_ALL_USERS: {
			const { userGroups } = action;
			console.log('userGroups', userGroups)
			let arr: IUser[] = [];
			userGroups.map(g => {
				arr = arr.concat(g.users)
			});
			console.log('arr', arr)
			return {
				...state,
				//users: action.users.map(user => ({...user}) ),
				userGroups,
				userOptions: arr.map(user => ({ label: user.name, value: user.userId }))
			};
		}

		case UserActionTypes.GET_USER: {
			return {
				...state,
				user: action.user
			};
		}

		default:
			return state;
	}
};