// Import redux types
import { ActionCreator, Dispatch } from 'redux';
import { ThunkAction } from 'redux-thunk';
// Import User Typing
import { IUser, IUserGroup, IUserGroupJson, IUsersState } from './types';
import data from "./data.json"
//const storageUsersDemo: IUserGroupJson[] = data;
const parseFromJson = (): IUserGroup[] => {
	return data.map(g => {
		return parseGroup(g)
	})
}

const parseFromLocalStorage = (arr: IUserGroupJson[]) : IUserGroup[] =>  {
	return arr.map(g => parseGroup(g));
}

const parseGroup = (g: IUserGroupJson): IUserGroup => ({
	...g,
	users: g.users.map(u => ({ ...u, created: new Date(u.created) })),
	created: new Date(g.created)
})

// Create Action Constants
export enum UserActionTypes {
	GET_ALL_USERS = 'GET_ALL_USERS',
  	GET_USER = 'GET_USER'
}

// Interface for Get All Action Type
export interface IGetAll {
	type: UserActionTypes.GET_ALL_USERS;
	userGroups: IUserGroup[];
 }
 
export interface IGet {
	type: UserActionTypes.GET_USER;
	user: IUser;
}

// localStorage
export const SUPPORT_USERS = 'SUPPORT_USERS' 
const storageUsers: IUserGroup[] = [
]


// Combine the action types with a union (we assume there are more)
export type UserActions = IGetAll | IGet;

const isWebStorageSupported = () => 'localStorage' in window

// Get All Action <Promise<Return Type>, State Interface, Type of Param, Type of Action>
export const getAllUsers: ActionCreator<
  ThunkAction<Promise<any>, IUsersState, null, IGetAll>
> = () => {
  return async (dispatch: Dispatch) => {
    try {
		// const response = await axios.get('https://swapi.co/api/people/');
		let userGroups: IUserGroup[] = [];
		if (isWebStorageSupported()) {
			const sUserGroups = localStorage.getItem(SUPPORT_USERS);
			if (sUserGroups !== null) {
				//const Users: IUserGroup[] = JSON.parse(sUsers);
				//Users.map(g => storageUsers.push(g))
				const parsed = JSON.parse(sUserGroups);
				userGroups = parseFromLocalStorage(parsed);
			}
			else {
				userGroups = parseFromJson() //parseFromJson().map(g => storageUsers.push(g))	
			}
		}
		else {
			//parseFromJson.map(g => storageUsers.push(g))
			userGroups = parseFromJson()
		}

		//const response = await getUsersFromLocalStorage(); 
      dispatch({
        type: UserActionTypes.GET_ALL_USERS,
        userGroups  //response.data.results,
      });
    } catch (err) {
      console.error(err);
    }
  };
};



