// Import Reducer type
import { Reducer } from 'redux';
import {
  FeatureActions,
  FeatureActionTypes
} from './actions';

import {IFeature, IFeatureState } from './types'

const initialFeature: IFeature = {
	featureId: 0,
	name: ''
};

const initialFeatureState: IFeatureState = {
	features: [],
	loading: false,
	formMode: 'display'
};


export const featureReducer: Reducer<IFeatureState, FeatureActions> = (
  state = initialFeatureState,
  action
) => {
  switch (action.type) {

   case FeatureActionTypes.GET_ALL_FEATURES: {
		return {
        ...state,
        features: action.features.map(feature => ({...feature}) ),
      };
	} 

 	case FeatureActionTypes.GET_FEATURE: {
      return {
		  ...state,
        feature: action.feature
      };
	}    

	default:
   		return state;
  }
};