// Import redux types
import { ActionCreator, Dispatch } from 'redux';
import { ThunkAction } from 'redux-thunk';
// Import Feature Typing
import { IFeature, IFeatureState } from './types';
import data from "./data.json"
const storageFeaturesDemo: IFeature[] = data;

// Create Action Constants
export enum FeatureActionTypes {
	GET_ALL_FEATURES = 'GET_ALL_FEATURES',
  	GET_FEATURE = 'GET_FEATURE'
}

// Interface for Get All Action Type
export interface IGetAll {
	type: FeatureActionTypes.GET_ALL_FEATURES;
	features: IFeature[];
 }
 
export interface IGet {
	type: FeatureActionTypes.GET_FEATURE;
	feature: IFeature;
}

// localStorage
export const SUPPORT_FEATURES = 'SUPPORT_FEATURES' 
const storageFeatures: IFeature[] = [
]


// Combine the action types with a union (we assume there are more)
export type FeatureActions = IGetAll | IGet;

const isWebStorageSupported = () => 'localStorage' in window

// Get All Action <Promise<Return Type>, State Interface, Type of Param, Type of Action>
export const getAllFeatures: ActionCreator<
  ThunkAction<Promise<any>, IFeatureState, null, IGetAll>
> = () => {
  return async (dispatch: Dispatch) => {
    try {
		// const response = await axios.get('https://swapi.co/api/people/');
		if (isWebStorageSupported()) {
			const sFeatures = localStorage.getItem(SUPPORT_FEATURES);
			if (sFeatures !== null) {
				const Features: IFeature[] = JSON.parse(sFeatures);
				Features.map(g => storageFeatures.push(g))
			}
			else {
				storageFeaturesDemo.map(g => storageFeatures.push(g))	
			}
		}
		else {
			storageFeaturesDemo.map(g => storageFeatures.push(g))
		}

		//const response = await getFeaturesFromLocalStorage(); 
      dispatch({
        type: FeatureActionTypes.GET_ALL_FEATURES,
        features: storageFeatures //response.data.results,
      });
    } catch (err) {
      console.error(err);
    }
  };
};



